var Page_nums = getCurrentPages();
var ID;
export default {
	data() {
		return {
			PJ_Title:"",
			PJ_Content:"",
			PJ_ID:""
		}
	},
	methods: {
		Publish(){
			uni.setStorage({
				key:"PJ_ID",
				data: ID
			})
			uni.setStorage({
				key:ID + "PJ_Title",
				data: this.PJ_Title
			})
			uni.setStorage({
				key:ID + "PJ_Content",
				data: this.PJ_Content
			})
			
			uni.navigateBack({
				// 发布后返回上一页
				delta: Page_nums
			})
		}
	},
	onLoad() {
		uni.getStorage({
			key:"PJ_ID",
			success: (res) => {
				ID = res.data;
				ID += 1;
			},
			fail() {
				ID = 1;
			}
		})
	}
}
